import 'piral/polyfills'
import * as React from 'react';
import { createRoot } from 'react-dom/client';
import { createInstance, Piral, createStandardApi } from 'piral';
import { layout, errors } from './layout';
import {demoPilet} from './local-pilet/demo-pilet'
// change to your feed URL here (either using feed.piral.cloud or your own service)
// const feedUrl = 'https://feed.piral.cloud/api/v1/pilet/sample'
const feedUrl = 'https://feed.piral.cloud/api/v1/pilet/empty';

const instance = createInstance({
  state: {
    components: layout,
    errorComponents: errors,
  },
  plugins: [...createStandardApi()],
  availablePilets: [demoPilet],
  requestPilets() {
    return fetch(feedUrl,{
      mode: 'no-cors'
    })
      .then((res) => {
        console.log('resjson',res)
        console.log('resjson',res.json())
        return res.json()
      })
      .then((res) => {
        console.log('res',res)
        return res.items
      });
  },
});

const root = createRoot(document.querySelector('#app'));

root.render(<Piral instance={instance} />);
